<template>
  <div id="team_page" class="team_page">
    <div id="title_part" class="title_part">
      <div id="title" class="title">{{ global.lang_pack.our_team }}</div>
      <div id="title_line" class="title_line"></div>
    </div>

    <div class="team_part">
      <div class="team_img_part">
        <div class="team_img_box">
          <img
            class="team_img"
            src="/static/team/christy.png"
          />
        </div>
        <div
          class="team_text_line"
        ></div>
        <div class="member_name">
          {{ global.lang_pack.christy }}
        </div>
      </div>
      <div>
      </div>
      <div class="team_text_part">
        <div class="team_idea">
          {{ global.lang_pack.christy_idea }}
        </div>
        <div class="team_text_line"
        ></div>
        <div class="team_intro">
          {{ global.lang_pack.christy_intro }}
        </div>
      </div>
    </div>

    <div class="team_part">

      <div class="team_img_part">
        <div class="team_img_box">
          <img
            class="team_img"
            src="/static/team/max.png"
          />
        </div>
        <div
          class="team_text_line"
        ></div>
        <div class="member_name">
          {{ global.lang_pack.max }}
        </div>
      </div>

      <div class="team_text_part">
        <div class="team_idea">
          {{ global.lang_pack.max_idea }}
        </div>
        <div
          class="team_text_line team_text_line_2"
        ></div>
        <div class="team_intro">
          {{ global.lang_pack.max_intro }}
        </div>
      </div>
    </div>

    <div id="team_part" class="team_part">
      <div class="team_img_part">
        <div class="team_img_box">
          <img
            class="team_img"
            src="/static/team/evan.png"
          />
        </div>
        <div
          class="team_text_line"
        ></div>
        <div class="member_name">
          {{ global.lang_pack.evan }}
        </div>
      </div>
      <div id="team_text_part" class="team_text_part">
        <div id="team_idea" class="team_idea">
          {{ global.lang_pack.evan_idea }}
        </div>
        <div
          class="team_text_line team_text_line_3"
        ></div>
        <div id="team_intro" class="team_intro">
          {{ global.lang_pack.evan_intro }}
        </div>
      </div>
    </div>
    <Footer></Footer>
    <Menu></Menu>
    <LangTransfer></LangTransfer>
  </div>
</template>

<style lang="css" scoped>
div {
  font-family: HelveticaNeueLTProLtEx;
  color: #37383d;
}

::-webkit-scrollbar {
  display: none;
}

div.team_page {
  background: #f7f7f7;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
}

div.title_part {
  margin-top: 100rem;
  margin-left: 300rem;
}

div.title {
  color: #251714;
  font-family: HelveticaNeueLTProEx;
  font-size: 100rem;
  text-transform: uppercase;
}

div.title_line {
  /* position: absolute; */
  background: #efcd24;
  width: 500rem;
  height: 8rem;
  left: 625rem;
}

div.team_part {
  /* background: #ffffff; */
  margin-top: 100rem;
  display: flex;
  flex-direction: row;
  padding-left: 300rem;
  padding-right: 300rem;
  align-items: center;
  /* height: 40rem; */
}

div.team_img_part {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

div.team_img_box {
  width: 300rem;
  height: 300rem;
  overflow: hidden;
  border-radius: 100%;
  border: solid 10rem #efcd24;
}

img.team_img {
  object-fit: cover;
  width: 300rem;
  height: 300rem;

  transition: all 0.2s;
  filter: grayscale(100%);
}

img.team_img:hover {
  filter: grayscale(0%);
  transform: scale(1.1);
}

div.member_name {
  font-family: HelveticaNeueLTProEx;
  font-size: 40rem;
  display: inline-block;
  color: #251714;
}

div.team_text_part {
  margin-left: 100rem;
  align-self: flex-start;
}

div.team_idea {
  color: #251714;
  font-family: HelveticaNeueLTProEx;
  font-size: 45rem;
}

div.team_text_line {
  margin-top: 20rem;
  height: 2rem;
  width: 100rem;
  display: inline-block;
  background: #251714;
}

/*div.team_name {*/
/*  font-family: HelveticaNeueLTProEx;*/
/*  font-size: 45rem;*/
/*  color: #251714;*/
/*}*/

div.team_intro {
  text-align: justify;
  text-justify: distribute-all-lines;
  letter-spacing: 0.5px;
  line-height: 2;
  /*width: 100rem;*/
  font-size: 18px;
}
</style>

<script>
import global from "../global";
import anime from "../../../modules/anime";
import Menu from "../../components/menu/Menu";
import LangTransfer from "../../components/lang_transfer/LangTransfer"
import Footer from "../../components/footer/Footer"

//export default
export default {
  name: "Team",
  data() {
    return {
      global: global,
    };
  },
  components: {
    Menu,
    Footer,
    LangTransfer
  },
  beforeCreate() {
    //set documentElement root size and language package
    global.change_root_size();
    global.set_lang_pack();
  },
  created() {
  },
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.change_root_size();
      })();
    };
    this.refresh_root_size();
    this.start_why_3_check_anime();
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
  },
  destroyed() {
  },
  watch: {
    // watch the size change to refresh
    "global.width": function (val, oldVal) {
      this.refresh_root_size();
    },
  },
  methods: {
    refresh_root_size() {
      let static_piexl = 700;
      if (global.width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize = global.width / 1920 + "px";
    },
    start_why_3_check_anime() {
      anime({
        // targets:"div."
      });
    },
  },
};
</script>



